// ------------------------------------------------------------------------------
//   @group prettyprint.js styling
// ------------------------------------------------------------------------------
/* Pretty printing styles. Used with prettify.js. */
/* SPAN elements with the classes below are added by prettyprint. */
.pln {
  color: #000;
}

/* plain text */
.str {
  color: #080;
}

/* string content */
.kwd {
  color: #008;
}

/* a keyword */
.com {
  color: #800;
}

/* a comment */
.typ {
  color: #606;
}

/* a type name */
.lit {
  color: #066;
}

/* a literal value */
/* punctuation, lisp open bracket, lisp close bracket */
.pun,
.opn,
.clo {
  color: #660;
}
.tag {
  color: #008;
}

/* a markup tag name */
.atn {
  color: #606;
}

/* a markup attribute name */
.atv {
  color: #080;
}

/* a markup attribute value */
.dec,
.var {
  color: #606;
}

/* a declaration; a variable name */
.fun {
  color: red;
}

/* a function name */
/* Use higher contrast and text-weight for printable form. */
@media print,
projection {
  .str {
    color: #060;
  }
  .kwd {
    color: #006;
    font-weight: bold;
  }
  .com {
    color: #600;
    font-style: italic;
  }
  .typ {
    color: #404;
    font-weight: bold;
  }
  .lit {
    color: #044;
  }
  .pun,
  .opn,
  .clo {
    color: #440;
  }
  .tag {
    color: #006;
    font-weight: bold;
  }
  .atn {
    color: #404;
  }
  .atv {
    color: #060;
  }
}

/* Specify class=linenums on a pre to get line numbering */
ol.linenums {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border: 1px solid #ddd;
  li {
    min-height: 24px;
    padding: 0 10px;
    background: #fff;
    ~ li {
      border-top: 1px solid #eee;
    }
    &:first-child {
      padding-top: 3px;
    }
  }
  /* Alternate shading for lines */
  li.L0,
  li.L2,
  li.L4,
  li.L6,
  li.L8 {
    background: #fcfcfc;
  }
}
